@import "media";

// spacers
$spacers: (
  0: 0em,
  1: 1em,
  2: 1.5em,
  3: 3em,
  4: 4.5em,
  // 5: 6em,
);

$margins: (
  "m": "margin",
  "mt": "margin-top",
  "mr": "margin-right",
  "mb": "margin-bottom",
  "ml": "margin-left",
  "mx": "margin-inline",
  "my": "margin-block",
);

$paddings: (
  "p": "padding",
  "pt": "padding-top",
  "pr": "padding-right",
  "pb": "padding-bottom",
  "pl": "padding-left",
  "px": "padding-inline",
  "py": "padding-block",
);

$gaps: (
  "g": "gap",
  "gx": "row-gap",
  "gy": "column-gap",
);

// parts
$parts: (
  0: 0%,
  25: 25%,
  50: 50%,
  75: 75%,
  100: 100%,
);

$widths: (
  "w": "width",
  // "wmax": "max-width",
  // "wmin": "min-width",,
);

$heights: (
  "h": "height",
  // "hmax": "max-height",
  // "hmin": "min-height",,
);

// flex
$alignsSelf: (
  "start": "flex-start",
  "end": "flex-end",
  "center": "center",
  // "stretch": "stretch",
);

$alignsAdd: (
  "between": "space-between",
  "around": "space-around",
  // "evenly": "space-evenly",
);

$flexs: (
  "jc": "justify-content",
  "ai": "align-items",
  // "ac": "align-content",
);
$flexsSelf: (
  "js": "justify-self",
  "as": "align-self",
);

// displays
$displays: (
  "none": "none",
  "flex": "flex",
  "block": "block",
  "inline": "inline",
);

@mixin create-property-each($array, $values, $bp-key) {
  @each $p-key, $p-value in $array {
    @each $s-key, $s-value in $values {
      @if $bp-key == "nil" {
        .#{$p-key}-#{$s-key} {
          #{$p-value}: #{$s-value} !important;
        }
      }
      @if $bp-key != "nil" {
        .#{$p-key}-#{$bp-key}-#{$s-key} {
          #{$p-value}: #{$s-value} !important;
        }
      }
    }
  }
}

@mixin create-properties($properties) {
  @each $array, $values in $properties {
    @include create-property-each($array, $values, "nil");
    @each $bp-key, $bp-value in $breakpoints {
      @include from($bp-value) {
        @include create-property-each($array, $values, $bp-key);
      }
    }
  }
}

$properties: (
  (
    $margins,
    map-merge(
      $map1: $spacers,
      $map2: (
        "auto": "auto",
      ),
    )
  ),
  ($paddings, $spacers),
  ($gaps, $spacers),
  ($widths, $parts),
  ($heights, $parts),
  (
    $flexs,
    map-merge(
      $map1: $alignsSelf,
      $map2: $alignsAdd,
    )
  ),
  ($flexsSelf, $alignsSelf),
  (
    (
      "d": "display",
    ),
    $displays
  ),
  (
    (
      "fd": "flex-direction",
    ),
    (
      "row": "row",
      "col": "column",
    )
  ),
  (
    (
      "ta": "text-align",
    ),
    (
      "start": "start",
      "end": "end",
      "center": "center",
    )
  )
);

@include create-properties($properties);
